@import url("https://fonts.googleapis.com/css?family=Baloo+Thambi+2&display=swap");

* {
  margin: 0;
  padding: 0;
}

.body {
  background-color: #212529;
}
/* Detail */

.title {
  /* font-size: calc(8px + 2.5vmin); */
  font-size: 1.7rem;
  color: turquoise;
  /* padding-left: 1.5rem; */
  /* padding-bottom: none; */
  z-index: 1;
}

.title2 {
  font-size: 1.7rem;
  color: turquoise;
  opacity: 1 !important;
  /* padding-bottom: 4px;
  margin-right: 3px; */
}

.subtitle {
  font-size: 1.2rem;
  color: #ccc;
}

.icon {
  /* margin: -3px; */
}

.badge {
  position: absolute;
  top: -2px;
  right: 10px;
}

.col {
  max-width: 50% !important;
  /* border: 1px solid grey; */
}

/* .HorizontalBar {
  margin-left: 10px !important;
} */

.box {
  /* border: 2px solid currentColor; */
  border: 1px solid #2a3d3d;
  border-radius: 5px;
  text-align: center;
  align-items: center;
  display: block;
  justify-content: center;
  background: #212529;

  /* background: #212529;
  background: linear-gradient(180deg, #2a2f33 0%, #212529 35%, #212529 100%); */
}

/* Sidebar */

.sidebar {
  width: 100%;
  height: 0vh;
  display: flex;
  flex-wrap: wrap;
}

/* canvas.chartjs-render-monitor {
  color: #ffffff;
} */

.side {
  background: #212529;
  opacity: 0.98;
  height: 100vh;
  font-size: calc(20px + 2.5vmin);
  /* color: rgb(45, 182, 130); */
  /* border: 1px solid turquoise; */
  /* border-radius: 5px; */
  overflow: hidden;
}

.visible {
  z-index: 10;
}

.hidden {
  display: none;
}

@media only screen and (min-width: 930px) {
  .side {
    width: 25vw;
  }
}

/* @media only screen and (min-width: 1130px) {
  .side {
    width: 20vw;
  }
} */

/* Buttons */

.button {
  height: 3.5rem;
  width: 6rem;
  z-index: 2;
  color: rgb(31, 174, 160) !important;
  outline: 1px solid rgb(31, 174, 160) !important;
  margin: 5px;
}

.button:focus {
  background-color: rgb(31, 174, 160) !important;
}

.close {
  height: 2rem;
  width: 2rem;
  /* margin-right: 19px; */
  /* margin-top: 10px; */
  padding: 0px 10px 3px 10px;
  color: rgb(31, 174, 160) !important;
  outline: 2px solid rgb(31, 174, 160) !important;
  z-index: 10 !important;
}

.button:hover {
  background-color: rgb(31, 174, 160) !important;
  color: #fff !important;
}

.button:focus {
  background-color: rgb(31, 174, 160) !important;
  color: #fff !important;
}

.number {
  font-family: "Baloo Thambi 2", cursive;
}

/* Tabs */

/* .wrapper {
  margin: 5px 0px;
} */

.tabs {
  display: flex;
  justify-content: left;
  /* padding-top: 0.7rem; */
}

.tabitem {
  cursor: pointer;
  height: 20px;
  text-align: left;
  /* padding: 5px; */
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  /* margin-right: 30px; */
}


.tabitem--inactive {
  opacity: 0.5;
  filter: grayscale(100%);
}

.tabitem--inactive:hover {
  opacity: 1;
}

.tabitem__icon {
  color: rgba(255, 255, 255, 0.9);
  font-size: 8px;
}

.tabitem__title {
  color: rgba(255, 255, 255, 0.9);
  font-size: 14px;
}

.content {
  text-align: center;
  /* margin-top: 5px; */
}

