a.active:hover {
  color: #f18e0c;
}

/* Scroll */

* {
  scrollbar-width: auto;
  scrollbar-color: #2b2d56 #3e3838;
}

*::-webkit-scrollbar {
  width: 8px;
}

*::-webkit-scrollbar-track {
  background: #212529;
}

*::-webkit-scrollbar-thumb {
  background: #212529 !important;
  border-radius: 5px;
  border: 1px solid teal;
}

